import React from 'react'
import {BrowserRouter as Router,Route,NavLink,Switch,Redirect} from 'react-router-dom'
import Txt from './Txt'
import Course from './Course'
import Blog from './Blog'
import Sercity from './Sercity'
import Header from './Header'
import Login from './Login'

/**
 * 路由基本配置
 * 路由传参（以及获取参数-props）
 * 动态路由（/:page）
 * js跳转
 * 权限路由
 */


function PrivateRoute({ component:Com, ...rest }) {
	return (
	  <Route
		{...rest}
		render={({ location }) =>
		   localStorage.getItem('userinfo')
		   ?<Com /> 
		   : (
			<Redirect
			  to={{
				pathname: "/login",
				state: location 
			  }}
			/>
		  )
		}
	  />
	);
  }


const Home = (props) => {
    console.log(props);
    return(
        <div style={{textAlign:'center'}}>
            <h2>React</h2>
            <p>用于构架用户界面的 JavaScript 库</p>
            <div>
                <button onClick={() => props.history.push('/txt')}>快速开始</button>
                <button onClick={() => props.history.push('/course')}>入门教程</button>
            </div>
        </div>
    )
}


const React11 = () => {
    return<Router>
        <Header />

        <Switch>
          {/* home组件这种配置方式会在Home组件的props */}
            <Route exact path='/' component={Home}/>
            <PrivateRoute path='/txt' component={Txt}/>
            {/* <Route path='/txt/:id' component={Txt}/> */}
            <PrivateRoute path='/course' component={Course}/>
            <Route path='/blog' component={Blog}/>
            <Route path='/sercity' component={Sercity}/>
            <Route path='/login' component={Login}/>
            {/* no-match */}
        </Switch>
    </Router>
}


export default React11;


