import React from 'react'
import {NavLink,Route,Redirect,Switch,useHistory,useLocation,useParams,useRouteMatch,withRouter} from 'react-router-dom'

const RouteWithSubRoutes = (route) => {
    if(route.pri){
        return <PrivateRoute {...route}/>
    }
    
    return (
        <Route
          path = {route.path}
          render = {props => (
            <route.component {...props} routes={route.routes} />
          )}
        />
      );
}

function PrivateRoute({ component:Com,routes:abc, ...rest }) {
	return (
	  <Route
		{...rest}
		render={({ location }) =>
		   localStorage.getItem('userinfo')
		   ?<Com {...rest} routes={abc}/> 
		   : (
			<Redirect
			  to={{
				pathname: "/login",
				state: location 
			  }}
			/>
		  )
		}
	  />
	);
  }

export default RouteWithSubRoutes
