import React from 'react'
import {BrowserRouter as Router,Route,NavLink,Switch,Redirect,withRouter} from 'react-router-dom'
import {Button,TabBar} from 'antd-mobile'

class Taobao extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'blueTab',
    };
  }

  render() {
    const {pathname} = this.props.location
    return (
      <div style= {{ position: 'fixed', width: '100%', bottom: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          barTintColor="white"
          hidden={this.state.hidden}
        >
          <TabBar.Item
            title="首页"
            key="Home"
            icon={<i style={{fontSize:21}} className='iconfont icon-shouye'></i>}
            selectedIcon={<i style={{fontSize:25}} className='iconfont icon-shouye1'></i>}
            selected={pathname === '/home'}
            // badge={1}
            onPress={() => {
              this.props.history.push('/home');
              console.log(this.props);
            }}
            data-seed="logId"
          >
              没事来逛逛
          </TabBar.Item>
          <TabBar.Item
            icon={<i style={{fontSize:21}} className='iconfont icon-gouwuchekong'></i>}
            selectedIcon={<i style={{fontSize:21}} className='iconfont icon-gouwucheman'></i>}
            title="购物车"
            key="gouwuche"
            // badge={'new'}
            selected={pathname === '/koubei'}
            onPress={() => {
              this.props.history.push('/koubei')
            }}
            data-seed="logId1"
          >
            <Button onClick={() => {
              this.props.history.push('/txt')
            }} type='primary'>ToDoc</Button>
              看看攒了多少了？
          </TabBar.Item>
          <TabBar.Item
            icon={<i style={{fontSize:21}} className='iconfont icon-dingdan1'></i>}
            selectedIcon={<i style={{fontSize:21}} className='iconfont icon-dingdan'></i>}
            title="订单"
            key="dingdan"
            // dot
            selected={this.state.selectedTab === 'greenTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'greenTab',
              });
            }}
          >
              订单，消费挺多啊
          </TabBar.Item>
          <TabBar.Item
            icon={<i style={{fontSize:21}} className='iconfont icon-wode'></i>}
            selectedIcon={<i style={{fontSize:21}} className='iconfont icon-wodedx'></i>}
            title="我的"
            key="wode"
            selected={this.state.selectedTab === 'yellowTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'yellowTab',
              });
            }}
          >
              啥就我的，没钱
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}


export default withRouter(Taobao);