import React from 'react'
import Txt from './pages/Txt'
import Course from './pages/Course'
import Blog from './pages/Blog'
import Sercity from './pages/Sercity'
import Login from './pages/Login'
import Home from './components/Home'

const Core = () => {
    return (
        <h1>core</h1>
    )
}

const Guide = () => {
    return (
        <h1>guide</h1>
    )
}

const Api = () => {
    return (
        <h1>api</h1>
    )
}

const Hook = () => {
    return (
        <h1>hooks</h1>
    )
}


const routes = [
    {
        path: '/',
        exact: true,
        component: Home,
        pri: false
    },
    {
        path: '/txt',
        component: Txt,
        pri: true,
        have:true,
        routes: [
            {
                path: '/txt/core',
                component: Core,
            },
            {
                path: '/txt/guide',
                component: Guide
            },
            {
                path: '/txt/api',
                component: Api
            },
            {
                path: '/txt/hooks',
                component: Hook
            }
        ]
    },
    {
        path: '/course',
        component: Course,
        pri: true,
        have:false
    },
    {
        path: '/blog',
        component: Blog,
        pri: false
    },
    {
        path: '/sercity',
        component: Sercity,
        pri: false
    },
    {
        path: '/login',
        component: Login
    }
    
]

export default routes;