import React, { Component } from 'react'
import {NavLink} from 'react-router-dom'
import './Header.css'
const Header = () => {
        return(
        <header>
            <nav>
                <NavLink activeClassName='active' exact to = '/'>开始</NavLink>
                <NavLink activeClassName='active' to = '/txt'>文档</NavLink>
                <NavLink activeClassName='active' to = '/course'>教程</NavLink>
                <NavLink activeClassName='active' to = '/blog'>博客</NavLink>
                <NavLink activeClassName='active' to = '/sercity'>社区</NavLink>
            </nav>
        </header>
        )
}

export default Header